.header {
  height: 12vh;
  padding: 0 8rem;
  background-color: #4e598c;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__title {
  font-size: 2.75rem;
  font-weight: 500;
  color: white;
}

.header a {
  text-decoration: none;
}

@media only screen and (max-width: 738px) {
  .header {
    padding: 0 3rem;
  }
}
